import React from 'react'
import './style.css'
import iVegies from './images/vegies-left.jpg'
import Nav from './Nav'
import Button from '../Button'

const Header = (props) => {
  return (
    <div className='header'>
      <div className='header-left'>
        <img className='img-responsive' src={iVegies} />
      </div>
      <div className='header-right'>
        <div className='sign-in'>
          {
            props.signedIn
              ? <div className='sign-in'>
                <Button onClick={props.handleEditMenuClick}>Edit Menu</Button>
                <Button onClick={props.handleSignInClick}>Sign-out</Button>
              </div>
              : <div className='sign-in'>
                <Button onClick={props.handleSignInClick}>Sign-in</Button>
              </div>
          }
        </div>
        <div className='header-titles'>
          <h1>Wholesome Foods</h1>
          <h2>Delicious meals made with locally sourced organic ingredients.</h2>
        </div>
        <Nav />
      </div>
    </div>
  )

}

export default Header



